import React, { useState, useEffect, useContext } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';
import { stockcontext } from './App.js';
import { Navbar, Container, Nav, NavDropdown, Jumbotron, Button, Row} from 'react-bootstrap';

let box = styled.div`
  padding : 20px;
`;
let 제목 = styled.h4`
  font-size: 25px;
  color: ${ props => props.색상}
`;



function Detail(props){
  let [alert, setAlert] = useState(true);
  let [inputData, setInputData] = useState('');
  let stock = useContext(stockcontext);
  let [tab, setTab] = useState(0);
  // useEffectHook 사용 5 초후 Alert 창 사라짐
  useEffect(()=>{
    let 타이머 = setTimeout(()=>{setAlert(false)},5000);
    return ()=> { clearTimeout(타이머)}
  },[alert]);
// alert on 일때만 useEffect 실행
  
  let history = useHistory();
  let { id } = useParams();
  let itemSelected = props.shoes.find(shoes => shoes.id == id);

  return (
    <> //JSX fragments allows multiple returns
    <div className="container">
      <box> 
        <제목 색상="blue" >Detail</제목>
        <제목 색상={'red'} >Detail</제목>
      </box>
    // alert status 에 따라 Alert Modal 창 뛰움 
      {inputData}
      <input onChange={(e)=>{setInputData(e.target.value)}} />
        {
        alert === true
        ? ( <div className="my-alert2">
            <p>재고가 얼마 남지 않았습니다</p>
          </div>)
        : null
      }
 
      <div className="row">
          <div className="col-md-auto">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
        
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{itemSelected.title}</h4>
            <p>{itemSelected.content}</p>
            <p>{itemSelected.price}원</p>
            <p> Stock: {stock[id]} </p>
          
            <button className="btn btn-danger" onClick={()=>{
              props.setStock([8,11,12]) }}>주문하기</button>

            <button className="btn btn-danger" onClick={()=>{
              history.push('/');
            }}>뒤로가기</button>  
          </div>
      </div>
    </div>
    <div>
      <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
      <Nav.Item>
        <Nav.Link eventKey="link-0" onClick={()=>{setTab(0) }}>Home</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link eventKey="link-1" onClick={()=>{setTab(1) }}>Link 1</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link eventKey="link-2" onClick={()=>{setTab(2) }}>Link 2</Nav.Link>
      </Nav.Item>
      </Nav>
     
    </div>
 </> // JSX fragment
  )
};
export default Detail;